.About {
    display: flex;
    align-items: center;
    justify-content: center;
}

.About h1, h2 {
    color:springgreen;
}

.About .container {
    display: flex;
    align-items: center;
    justify-content: center;
    /* position:relative; */
}

.About .content {
    display: flex;
    flex-direction: column;
    width:45%;
    padding-top: 30px;
    margin-bottom:100px;
}

.experience p {
    margin: 0;
}

.About .headshot {
    max-width:33%;
    height:100%;
    padding-top:30px;
    /* position: absolute; */
}

.About .headshot img{
    width: 100%;
}